




























import { Component, Vue } from 'vue-property-decorator';
import { ITestContent } from '@/types/interface';
import { Action, State } from 'vuex-class';
import banner from '../../components/Banner.vue';
import testRecordPanel from '../../components/TestRecordPanel.vue';

@Component({
  components: {
    banner,
    testRecordPanel,
  },
})

export default class TestRecord extends Vue {
  @State('recordList', { namespace: 'onlineTest' }) stateRecordList!: ITestContent[];
  @Action('getMemberTestList', { namespace: 'onlineTest' }) getMemberTestList!: () => Promise<void>;
  @Action('postReturnTest', { namespace: 'onlineTest' }) postReturnTest!: (id: string | undefined) => Promise<void>;
  @Action('postCreateReTestList', { namespace: 'onlineTest' }) postCreateReTestList!: (id: string | undefined) => Promise<void>;
  @Action('saveTestCriteria', { namespace: 'onlineTest' }) saveTestCriteria!: (data: ITestContent) => Promise<void>;

  flag = 'all';
  list: ITestContent[] = [];

  get filterList() {
    return this.flag === 'all' ? this.list : this.list.filter((d: ITestContent) => {
      if (this.flag === 'done') return d.isFin === '1';
      return d.isFin === '0';
    });
  }

  get isTestProcess() {
    return this.$route.query.process === 'test';
  }

  created() {
    this.getMemberTestList()
      .then(() => {
        this.list = this.stateRecordList;
      });
  }

  link() {
    if (this.isTestProcess) this.$router.push('/test/set_condition');
    else this.$router.push('/member');
  }

  switchHandler(idx: string) {
    this.flag = idx;
  }

  select(id: string) {
    const test = this.list.find((d) => d.testId === id);
    if (!test) return;
    if (test.isFin === '0') this.$router.push('/test/main');
    else {
      this.saveTestCriteria(test)
        .then(() => this.postReturnTest(id))
        .then(() => {
          this.$router.push('/test/solution_page');
        });
    }
  }

  sameQuz(test: ITestContent) {
    this.saveTestCriteria(test)
      .then(() => this.postCreateReTestList(test.testId))
      .then(() => {
        this.$router.push('/test/main');
      });
  }

  sameCondition(item: ITestContent) {
    console.log(JSON.stringify(item));
  }
}
